
import {defineComponent} from 'vue'
import InputLabel from './InputLabel.vue';

export default defineComponent({
    components: {
        InputLabel,
    },
    emits: ['update:modelValue'],
    props: {
        label: {
            type: String,
            default: ""
        },

        type: {
            type: String,
            default: ""
        },
        placeholder: {
            type: String,
            default: ""
        },
        error: {
            type: String,
            default: ""
        },
        indication: {
            type: String,
            default: ""
        },
        name: {
            type: String,
            default: ""
        },
        modelValue: {
            type: String,
            default: ""
        },
        icon: {
            type: String,
            default: ""
        },
        right: {
            type: Boolean,
            default: false
        },
        autocomplete: {
            type: Boolean,
            default : false
        },
        string_before: {
          type: String,
          default : ""
        },
    },
    computed: {
        _autocmoplete: function() {
            if (this.autocomplete) {
                return "on";
            }
            return "off";
        }
    },
    methods: {
        onInput(event: InputEvent) {
            this.$emit('update:modelValue', this.string_before + (event.target as HTMLInputElement).value)
        }
    }
})
