
import {defineComponent} from 'vue'
import {mapGetters} from 'vuex'
import SearchBar from '@/components/navigation/SearchBar.vue'
import vClickOutside from '@/click-outside'

export default defineComponent({
    components: {
        SearchBar
    },
    directives: {
      clickOutside: vClickOutside
    },
    data() {
        return {
            display: false,
            display2: false,
            timeout: 100,
        }
    },
    computed: {
        ...mapGetters([
            'isAuthenticated',
        ])
    },
    methods: {
        onClickOutside() {
          this.display2 = false;
          setTimeout(() => {
            this.display = false;
          },this.timeout);
        },
        toggle: function() {
            if (this.display) {
                this.display2 = false;
                setTimeout(() => {
                    this.display = false;
                },this.timeout);

            } else {
                this.display = true;
                setTimeout(() => {
                    this.display2 = true;
                },this.timeout);
            }
        },
    }
})
