
import {defineComponent} from "vue";
import ResizeAuto from "../utils/ResizeAuto.vue";
import InputLabel from "./InputLabel.vue";

export default defineComponent({
  components: {
    ResizeAuto,
    InputLabel,
  },
  props: {
    placeholder: {
      type: String,
      default: "",
    },
    modelValue: {
      type: String,
      default: "",
    },
    name: {
      type: String,
      default: "",
    },
    label: {
      type: String,
      default: "",
    },
    maxLength: {
      type: Number,
      default: 250,
    },
    rows: {
      type: Number,
      default: 2,
    },
  },
  computed: {
    inputValue: {
      get(): string {
        return this.modelValue;
      },
      set(value: string) {
        this.$emit("update:modelValue", value);
      },
    },
  },
  methods: {
    send: function () {
      this.$emit("send");
    },
    newline: function () {
      this.inputValue = `${this.inputValue}\n`;
    },
  },
});
