
import { defineComponent, PropType } from "vue";
import InputLabel from "./InputLabel.vue";
import vClickOutside from "@/click-outside";
import { API } from "@/api";
import { User } from "@/models";

export default defineComponent({
  components: {
    InputLabel,
  },
  directives: {
    clickOutside: vClickOutside,
  },
  data() {
    return {
      users: new Array<User>(),
      selectedUsers: new Array<User>(),
      searchQuery: "",
      focus: false,
      timer: undefined as unknown as number,
    };
  },
  props: {
    label: {
        type: String,
        default: ""
    },
    name: {
        type: String,
        required: true
    },
    placeholder: {
        type: String,
        default: ""
    },
    error: {
        type: String,
        default: ""
    },
    indication: {
        type: String,
        default: ""
    },
    modelValue: {
        type: Object as PropType<Array<User>>,
        required: true
    }
  },
  methods: {
    updateUserList: function () {
      if (this.timer) clearTimeout(this.timer);

      this.timer = setTimeout(() => {
        if (this.searchQuery === "") {
          this.users = [];
          return;
        }

        API.User.search(this.searchQuery).then((response) => {
          switch (response.status) {
              case 200:
                  this.users = response.data;
                  break;

              default:
                  break;
          }
        });
      }, 250);
    },
    reset() {
        this.focus = false;
        this.users = [];
        this.searchQuery = "";
    },
    addUser(user: User) {
        if (this.selectedUsers.map((user) => user.id).includes(user.id)) return;

        this.selectedUsers.push(user);
        this.reset();
        this.$emit('update:modelValue', this.selectedUsers);
    },
    removeUser(user: User) {
      let index = this.selectedUsers.map((user) => user.id).indexOf(user.id);
      if (index > -1) {
        this.selectedUsers.splice(index, 1);
        this.$emit('update:modelValue', this.selectedUsers);
      }
    },
    onClickOutside: function () {
      this.focus = false;
    },
  },
})
