
import {defineComponent, PropType} from 'vue'
import {mapGetters} from 'vuex'
import TextArea from '@/components/inputs/TextArea.vue';
import vClickOutside from "@/click-outside";
import ModalComponent from '@/components/navigation/ModalComponent.vue'
import PostCard from '@/components/cards/PostCard.vue'
import ResizeAuto from '@/components/utils/ResizeAuto.vue'
import {Post, Project, User} from '@/models';
import {API} from '@/api';

interface FormFile {
  content: File | null | undefined;
  url: string;
}

export default defineComponent({
  components: {
    TextArea,
    ResizeAuto,
    ModalComponent,
    PostCard
  },
  directives: {
    clickOutside: vClickOutside
  },
  emits: {
    close: null
  },
  props: {
    extraContent: {
      type: Boolean,
      default: false
    },
    resharedPost: {
      type: Object as PropType<Post | undefined>,
      default: undefined
    }
  },
  data() {
    return {
      showSelect: false,
      showModal: false,
      content: "",
      AuthorIsProject: false,
      files: new Array<FormFile>(),
      author: undefined as unknown as User|Project,
      errors: []
    }
  },
  beforeMount() {
    this.author = this.user;
  },
  computed: {
    ...mapGetters([
      'isAuthenticated',
      'user',
    ])
  },
  methods: {
    onAuthorSelected: function (author:User|Project) {
      this.author = author;
      this.AuthorIsProject = !('username' in this.author);
      this.showSelect = false;
    },
    onClickOutside : function () {
      this.showSelect = false;
    },
    selectFile() {
      (this.$refs.picture as HTMLInputElement).click();
    },
    removeFile: function(index: number) {
      if (index <= this.files.length) {
        this.files.splice(index, 1);
      }
    },
    previewFile: function() {
      let file = (this.$refs.picture as HTMLInputElement)?.files?.item(0);
      if (file) {
        this.files.push({
          content: file,
          url: URL.createObjectURL(file)
        });
      }
    },
    openModal() {
      this.showModal = true;
    },
    closeModal() {
      this.showModal = false;
      this.$emit('close');
    },
    createPost() {
      API.Post.create((this.files.map(f => f.content) as File[]), this.resharedPost,
          this.content, this.AuthorIsProject ? (this.author as Project) : undefined).then(response => {
          switch (response.status) {
            case 201:
              this.errors = [];
              this.$router.push({name: 'post', params: {id: response.data.id}});
              break;

            case 422:
              this.errors = [];
              Object.values((response.data as any)['errors']).forEach((error: any) => {
                this.errors = this.errors.concat(error)
              })
              break;

            default:
              break;
          }
      })
    }
  }
})
