
import {defineComponent, PropType} from 'vue'
import vClickOutside from '../../click-outside'
import moment from 'moment';
import {mapGetters} from 'vuex'
import {API} from '@/api';
import lottie, {AnimationItem} from 'lottie-web';
import {Comment, Post} from '@/models'
import TagLabel from '../utils/TagLabel.vue'
import CommentCard from "@/components/cards/CommentCard.vue";
import TextArea from "@/components/inputs/TextArea.vue";

export default defineComponent({
  components: {
    TagLabel,
    TextArea,
    CommentCard
  },
  directives: {
    clickOutside: vClickOutside
  },
    data() {
      return {
        post_data: this.post as Post,
        likeAnimation: undefined as unknown as AnimationItem,
        loadingAnimation: undefined as unknown as AnimationItem,
        loadingComments: false,
        next_comment_page: 1,
        display_comments: true,
        comments: new Array<Comment>(),
        newCommentContent: ""
      }
    },
    props: {
        post: {
            type: Object as PropType<Post>,
            required: true,
            default: () => {return null;}
        },
    },
    mounted() {

      this.likeAnimation = lottie.loadAnimation({
        container: this.$refs['likeIcon'] as Element,
        renderer: 'svg',
        loop: false,
        autoplay: false,
        path: '/lottie/heart.json'
      });

      this.loadingAnimation = lottie.loadAnimation({
        container: this.$refs['loader'] as Element,
        renderer: 'svg',
        loop: true,
        autoplay: false,
        path: '/lottie/loading.json'
      });

      if (this.post_data.liked) {
        this.likeAnimation.play();
      }
    },
    computed: {
        timeSinceCreation(): string {
          return moment((this.post_data as Post).created_at).fromNow();
        },
        ...mapGetters({
            user: 'user',
            isAuthenticated : 'isAuthenticated'
        })
    },
    methods: {
        like: function(like: boolean) {
            if (!this.isAuthenticated) return;
            if (like) {

              API.Post.like(this.post).then(response => {
                if (response.status == 200) {
                  this.likeAnimation.setDirection(1);
                  this.likeAnimation.play();
                  this.post_data = response.data as Post;
                }
              })
            } else {

              API.Post.unlike(this.post).then(response => {
                if (response.status == 200) {
                  this.likeAnimation.setDirection(-1);
                  this.likeAnimation.play();
                  this.post_data = response.data as Post;
                }
              });
            }
        },
      showComments() {
        if (this.comments.length == 0) this.addComments();
        this.display_comments = true;
      },
      writeComment() {
        if (!this.isAuthenticated) return;
        API.Comment.create(this.newCommentContent, this.post).then(response => {
          switch (response.status) {
            case 201:
              this.comments.push(response.data);
              this.newCommentContent = "";
              break;
          }
        });
      },
      addComments() {
        if (this.next_comment_page == -1) return;
        this.loadingComments = true;
        this.loadingAnimation.play();
        API.Post.comments(this.post, this.next_comment_page).then(response => {
          switch (response.status) {
            case 200:
              this.comments.push(...response.data.data);

              if (this.next_comment_page == response.data.meta.last_page) this.next_comment_page = -1;
              else this.next_comment_page++;

              this.loadingComments = false;
              this.loadingAnimation.stop();
              break;
            default:
              break;
          }
        });
      }
    }
})
