
import { defineComponent } from 'vue'
import { API } from '../../api';
import vClickOutside from '../../click-outside'
import { Project, User } from '../../models';

export default defineComponent({
    directives: {
        clickOutside: vClickOutside
    },
    data() {
        return {
            focus: false,
            projects: new Array<Project>(),
            users: new Array<User>(),
            searchQuery: "",
            timer: undefined as unknown as number
        }
    },
    mounted() {
        // feather.replace();
    },
    computed: {
        displayResults() : boolean {
            return (this.focus && (this.users.length > 0 || this.projects.length > 0))
        }
    },
    methods: {
        onClickOutside(): void {
            this.focus = false;
        },
        reset(): void {
            this.focus = false;
            this.projects = [];
            this.users = [];
            this.searchQuery = "";
        },
        refreshLists(): void {
            if (this.timer) clearTimeout(this.timer);

            this.timer = setTimeout(() => {
                if (this.searchQuery !== "") {
                    API.Project.search(this.searchQuery).then(response => {
                        switch (response.status) {
                            case 200:
                                this.projects = response.data;
                                break;

                            default:
                                break;
                        }
                    });

                    API.User.search(this.searchQuery).then(response => {
                        switch (response.status) {
                            case 200:
                                this.users = response.data;
                                break;

                            default:
                                break;
                        }
                    });
                }
            }, 100);
        }
    }
})
