
import {defineComponent} from 'vue'
import {mapGetters} from 'vuex'
import {API} from '@/api'
import {User} from '@/models'
import ProjectCard from '@/components/cards/ProjectCard.vue'
import UserFollowButton from "@/components/buttons/UserFollowButton.vue";

export default defineComponent({
  components: {
    ProjectCard,
    UserFollowButton
  },
  data() {
    return {
      user: undefined as unknown as User
    }
  },
  beforeRouteEnter(to, from, next) {
    API.User.get(Number(to.params['id'])).then(response => {
      switch (response.status) {
        case 200:
          next(vm => {
            (vm as any).user = response.data
          })
          break;

        default:
          next(false)
          break;
      }
    });
  },
  beforeRouteUpdate(to, from, next) {
    API.User.get(Number(to.params['id'])).then(response => {
      switch (response.status) {
        case 200:
          this.user = response.data
          next();
          break;

        default:
          next(false);
          break;
      }
    });
  },
  mounted() {
    API.Notification.get().then(response => {
      API.Notification.markAsRead(response.data.data[0].id);
    })
  },
  computed: {
    ...mapGetters({
      authUser: 'user'
    })
  },
})
