
import {defineComponent} from 'vue'
import CustomInput from '@/components/inputs/CustomInput.vue'
import {mapActions, mapGetters} from 'vuex'
import {API} from '@/api'

interface IError {
  email: string,
  password: string
}

export default defineComponent({
  components: {
    CustomInput
  },
  data() {
    return {
      form: {
        email: "",
        password: ""
      },
      errors: {
        email: "",
        password: ""
      } as IError,
      credentialError: ""
    }
    },
    computed: {
        ...mapGetters({
            isAuthenticated: 'isAuthenticated'
        })
    },
    methods: {
        ...mapActions({
            me: 'me'
        }),
        resetErrors() {
            this.errors = {
                email: "",
                password: ""
            };

            this.credentialError = "";
        },
        async onSubmit() {

            await API.csrf();

            API.login(this.form).then(response => {
                this.resetErrors()

                switch (response.status) {
                    case 200:
                        // Authenticated
                        this.me().then(() => {
                            if (this.isAuthenticated)
                            {
                                this.$router.push({name: 'feed'});
                            }
                        })

                        break;

                  case 422:
                    for (let errorsKey in this.errors) {
                      this.errors[errorsKey as keyof IError] = response.data.errors[errorsKey] ? response.data.errors[errorsKey][0] : ""
                    }
                    this.credentialError = response.data.message;
                    break;

                    case 401:
                        this.credentialError = response.data;
                        break;
                    default:
                        break;
                }
            });

        }
    }
})
